import {
    canvas
} from 'roughjs/dist/rough.umd';
let isSame = []
const canv = document.getElementById('canv');
let c = canv.getContext('2d');
canv.width = window.innerWidth / 100 * 80;
canv.height = window.innerHeight /100 *80;

const ctx = canvas(canv);


let colors = ["rgb(255, 111, 97)", "rgb(107, 91, 149)", "rgb(42, 75, 124)",
    "rgb(38, 78, 54)", "rgb(121, 123, 58)", "rgb(42, 41, 62)", "rgb(254, 132, 14)"
];

class Nodes {
    constructor(a) {
        this.n = a
        this.w = canv.width;
        this.h = canv.height;
    }

    drawNodes() {
        const n = this.n;
        const x = (this.w / (this.n.length));
        for (let i = 1; i <= n.length; i++) {
            const r = Math.abs(Math.floor(i % 7) - 6);
            const color = colors[r]
            for (let j = 1; j <= n[i - 1]; j++) {
                const y = (this.h / n[i - 1]);
                ctx.circle(x * i - x / 2, y * j - y / 2, Math.min(x, y) / 2, {
                    fill: color,
                    fillWeight: 2,
                });
            }
        }
    }
    drawLines() {
        let n = this.n;
        let x = (this.w / (this.n.length));
        for (let i = 1; i <= n.length; i++) {
            for (let j = 1; j <= n[i - 1]; j++) {
                for (let k = 1; k <= n[i]; k++) {
                    const y1 = this.h / n[i - 1];
                    const y2 = this.h / n[i];
                    const h1 = Math.min(x, y1) / 4;
                    const h2 = Math.min(x, y2) / 4;
                    const x1t = x * i - x / 2;
                    const y1t = y1 * j - y1 / 2;
                    const x2t = x * (i + 1) - x / 2;
                    const y2t = y2 * k - y2 / 2;
                    ctx.line(x1t + h1, y1t * h1 / h1, x2t - h2, y2t);
                }
            }
        }
    }
}


function set(a) {
    if(a === [])return;
    let node = new Nodes(a);
    node.drawLines();
    node.drawNodes();
}
document.getElementById('num').oninput = function(){
    setI(this)
}

window.onresize = function() {
    canv.width = window.innerWidth / 100 * 80;
    canv.height = window.innerHeight /100 *80;
    setI(document.getElementById('num'),true);
}

function setI(t,isResize = false) {
    let v = t.value.split(' ').filter(x => x !== "")
       .map(Number).filter(x => !Number.isNaN(x));
    if(isSame.join() !== v.join() || isResize){
        c.fillStyle = 'white';
        c.fillRect(0, 0, canv.width, canv.height);
       set(v);
    isSame = JSON.parse(JSON.stringify(v));
    }
}

